import { Component, OnInit, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { AppointmentProfileListComponent } from 'src/app/modules/admin/appointment/appointment-profile/appointment-profile-list/appointment-profile-list.component';

import { AppointmentProfileListService } from 'src/app/modules/admin/appointment/appointment-profile/appointment-profile-list/appointment-profile-list.service';

import { ApiService } from 'src/app/core/api/api.service';

import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalsService } from 'src/app/modules/ui/modals/modals.service';

@Component({
  selector: 'app-appointment-view-profile',
  templateUrl: './appointment-view-profile.component.html',
  styleUrls: ['./appointment-view-profile.component.scss']
})
export class AppointmentViewProfileComponent implements OnInit {

  profile:any = {}

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private apiService: ApiService,
    private _changeDetectorRef: ChangeDetectorRef,
    private _appointmentProfileListComponent: AppointmentProfileListComponent,
    private appointmentProfileListService: AppointmentProfileListService,
    private sessionService: SessionService,
    private sharedService:SharedService,
    private modalsService: ModalsService
  ) { }

  goBack() {
    this._appointmentProfileListComponent.matDrawer.close();
    this.appointmentProfileListService.setAppointmentProfileListMatDrawer(null)
    this._router.navigate(['/appointment/profiles/list']);
  }

  ngOnInit(): void {

    this.appointmentProfileListService.appointmentprofilelostdrawer.subscribe((res:any) => {
      if(res.id != undefined && res.id == this._activatedRoute.params['value'].id){
        this._appointmentProfileListComponent.matDrawer.open();
        this.profile = res.data
      }
    })

  }

}
