import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { AppointmentProfileListService } from './appointment-profile-list.service';


@Injectable({
  providedIn: 'root'
})
export class AppointmentProfileListResolver implements Resolve<boolean> {

  constructor(
    private router: Router,
    private appointmentProfileListService: AppointmentProfileListService
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> {

    var data = this.appointmentProfileListService.getAppointmentProfileListMatDrawer();

    if(data == null){

      const parentUrl = state.url.split('/').slice(0, -3).join('/');

      // Navigate to parent url
      this.router.navigateByUrl(parentUrl);

    }


    return;

  }
}
