import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AppointmentProfileListResolver } from './appointment-profile-list.resolver';

import { AppointmentProfileListComponent } from './appointment-profile-list.component';
import { AppointmentViewProfileComponent } from './appointment-view-profile/appointment-view-profile.component';



const routes: Routes = [ {
  path: '', component: AppointmentProfileListComponent,
  children: [
    {
      path         : ':type/:id',
      component    : AppointmentViewProfileComponent,
      resolve: {
        AppointmentProfileListResolver
      }
    }
  ]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AppointmentProfileListRoutingModule { }
